import React from "react";
import Fade from "react-reveal/Fade";

import aboutImage from "../image/aboutImage.JPG";

import FooterName from "./footer";

class AboutComponents extends React.Component {
  render() {
    return (
      <div>
        <Fade left>
          <span role="img" alt="img" aria-label="okay">
            ‚ú®
          </span>{" "}
          <b style={{ fontSize: "20px" }}> Software Dev </b>
        </Fade>

        <div className="about-Photo-Edit">
          <img src={aboutImage} alt={aboutImage} />
        </div>

        <Fade right>
          <div className="about-me-details">
            <p>
              Hi, I'm Jeff, a Developer from Canada. I started my Dev journey 6
              years ago. I majored in Information Technology at the University
              of Ghana and worked as a web intern in a Broadcasting Network
              Corporation.
            </p>
            <p>
              After migrating to Canada, I gave a shot at bodybuilding sport,
              and I'm glad to have won some competitions in Toronto.
            </p>
            <p>
              During my journey in bodybuilding, I studied Software Engineering
              at Sheridan College. By focusing and enhancing my knowledge. I
              gained skills and increased learning which has brought me this far
              and while still improving on my skills.
            </p>
            <p>
              In a world of changes and everything been accessible with just a
              click, Programming has made us masters of controlling and making
              Objects seems more realistic and defines the word "Everything is
              Possible". IT is the new World and I do not doubt that it makes
              the world better!.
            </p>
            <p>if you want to know more about me, feel free to contact me!</p>
            <p />
            <p />
            <b />
          </div>
        </Fade>

        <FooterName />
      </div>
    );
  }
}

export default AboutComponents;
