import React from "react";
import git from "../image/github-logo.png";
import linkedin from "../image/linkedin.png";
import mail from "../image/mail.png";
import "./main.css";

const SocialComponent = () => {
  return (
    <div>
      <hr />
      <div className="links-social">
        <a href="mailto:odogbajeffrey@gmail.com">
          <img alt="mail" className="social" src={mail} />
        </a>
        <a
          href="https://github.com/JeffreyOdogba"
          target="_blank"
          rel="noopener noreferrer"
        >
          <img alt="git" className="social" src={git} />
        </a>
        <a
          href="https://www.linkedin.com/in/jeffreyodogba/"
          target="_blank"
          rel="noopener noreferrer"
        >
          <img alt="linkedin" className="social" src={linkedin} />
        </a>
      </div>
    </div>
  );
};

export default SocialComponent;
